import {
  AfterViewInit,
  ApplicationRef,
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  ContentChild,
  ChangeDetectorRef,
  Directive,
  ElementRef,
  HostListener,
  Inject,
  Injector,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  QueryList,
  Renderer2,
  ViewChild,
  ViewChildren,
  ViewContainerRef,
  forwardRef,
  HostBinding
} from '@angular/core';
import {Directionality} from '@angular/cdk/bidi';
import {ESCAPE, LEFT_ARROW, RIGHT_ARROW} from '@angular/cdk/keycodes';
import {transition, trigger, useAnimation} from '@angular/animations';

import {ANIMATION_NAME} from '../../common/animation';
import {AdaptDeviceDetectionService} from '../../common/device-detection.service';
import {AdaptFocusMonitorDirective} from '../../common/focus-monitor.directive';
import {AdaptFocusMonitorService} from '../../common/focus-monitor.service';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularCustomEventType,
  AdaptRadarAngularService
} from '../../common/radar/adapt-radar';
import {AdaptTreeStructureFilteringService} from '../../common/tree-structure-filter';
import {LayerService} from '../../common/layer-service';
import {PopupService} from '../../common/popup';
import {convertPlacement, PlacementArray, positionElements} from '../../common/positioning';
import {debounce, isEventFrom} from '../../common/utilities';
import {getInteractionSource} from '../../common/interaction-source';
import {getEventKeyCode} from '../../common/key-codes';

import {CascadeItem, MenuItem, MenuItemFilteringConfig} from '../models/menu-item';
import {MenuGlobalService} from '../menu-global.service';
import {MenuInnerService} from '../menu.service';
import {FocusOrigin} from '@angular/cdk/a11y';
import {SafeCdr} from '../../common/safe-cdr';

let _submenuItemTestIdIndex: number = 1;

@Component({
  selector: 'adapt-submenu',
  templateUrl: './submenu.component.html',
  animations: [
    trigger('menuAnimation', [
      transition('void => *', [
        useAnimation(ANIMATION_NAME.popIn)
      ]),
      transition('* => void', [
        useAnimation(ANIMATION_NAME.popOut)
      ])
    ])
  ],
  providers: [AdaptFocusMonitorService, AdaptTreeStructureFilteringService]
})
export class AdaptSubmenuComponent implements OnInit, AfterViewInit {
  nodes: MenuItem[];
  firstInit: boolean;
  anchor: AdaptMenuAnchorDirective;
  level: number;
  theme: string;
  isMobile: boolean;
  isRtl: boolean = false;
  subMenuHeader: string;
  filteringConfig?: MenuItemFilteringConfig;
  filteredNodes: MenuItem[];
  filterQuery: string = '';
  resultsFound: boolean = true;
  onFilteringDebounce: Function = debounce(this.onFiltering.bind(this), 100);
  eventType: FocusOrigin = null;
  makeItemActive: boolean;
  analyticsMetadata: {
    parentComponentName: AdaptRadarAngularCustomEventName;
  };

  @HostBinding('id') id: string;
  @HostBinding('class.adapt-submenu') defaultHostClass: boolean = true;

  @HostBinding('class.dark') get darkThemeClass(): boolean {
    return this.theme === 'dark';
  }

  @HostBinding('class.white') get whiteThemeClass(): boolean {
    return this.theme === 'white';
  }

  @HostBinding('class.grey') get greyThemeClass(): boolean {
    return this.theme === 'grey';
  }

  @HostBinding('class.custom') get customThemeClass(): boolean {
    return this.theme === 'custom';
  }

  @HostBinding('@.disabled') get isAnimationDisabled(): boolean {
    return this.level !== 0;
  }

  @HostBinding('@menuAnimation') menuAnimation: boolean = true;

  @ViewChildren(AdaptFocusMonitorDirective, {read: AdaptFocusMonitorDirective}) focusMonitoring: QueryList<AdaptFocusMonitorDirective>;
  @ViewChild('filterResults', {static: true}) filterResults: ElementRef;

  private _shiftTop: number = 5;
  private _placementRootMenu: PlacementArray = ['bottom-left', 'bottom-right', 'bottom-left'];
  private _placementSubmenu: PlacementArray = ['right-top', 'left-top', 'right-top'];

  constructor(private _menuInnerService: MenuInnerService,
              private _menuGlobalService: MenuGlobalService,
              private _elemRef: ElementRef,
              private _renderer: Renderer2,
              private _ngZone: NgZone,
              private deviceDetection: AdaptDeviceDetectionService,
              private _focusHandle: AdaptFocusMonitorService,
              private _dir: Directionality,
              private _cd: ChangeDetectorRef,
              private _adaptTreeStructureFilteringService: AdaptTreeStructureFilteringService,
              private _adaptRadarService: AdaptRadarAngularService) {
    this.isRtl = this._dir.value === 'rtl';
  }

  ngOnInit(): void {
    this.id = `adapt-submenu-${this.level}`;

    if (this.isRtl) {
      this._placementRootMenu = convertPlacement(this._placementRootMenu);
      this._placementSubmenu = convertPlacement(this._placementSubmenu);
    }

    if (this.isMobile) {
      this._menuInnerService.menuElementRef.next(this._elemRef);
    }
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  trackByFn(index: number, item: any): any {
    return item.id || index;
  }

  ngAfterViewInit(): void {
    if (!this.isMobile && this._menuInnerService.appendToBody) {
      document.body.appendChild(this._elemRef.nativeElement);
    }

    if (this.focusMonitoring) {
      this._focusHandle.init(this.focusMonitoring);
    }

    if (this.anchor && !this.isMobile) {
      this._applyPosition();
    }
  }

  @HostListener('keydown', ['$event'])
  handleKeyDown(event: KeyboardEvent): void {
    if (event.currentTarget !== this._menuInnerService.currentActiveCascadeItem) {
      return;
    }

    const keyCode = getEventKeyCode(event);

    if (keyCode === ESCAPE) {
      this._menuInnerService.detachCascade();
    }

    if (keyCode === (this.isRtl ? RIGHT_ARROW : LEFT_ARROW)) {
      this.anchor.toggleOpen(event);

      return;
    }

    if (keyCode === (this.isRtl ? LEFT_ARROW : RIGHT_ARROW)) {
      return;
    }

    this._focusHandle.onKeyDown(event);
  }

  handleSubMenuLinkClick(item: MenuItem): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptMenu, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      ParentComponent: this.analyticsMetadata?.parentComponentName,
      Name: item.label,
      Disabled: item.disabled,
      Type: 'link',
      SiblingsOptionsCount: this._getSiblingsCount(),
      HiddenSiblingsOptionsCount: this._getHiddenSiblingsCount()
    });

    if (item.closeOnClick) {
      this.closeMenu();
    }
  }

  onActionClick($event: MouseEvent, item: MenuItem): void {
    this.selectMenuItem(item);

    if (item.action) {
      item.action($event, item.id);
    }

    if (item.closeOnClick) {
      this.closeMenu();
    }
  }

  selectMenuItem(item: MenuItem): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptMenu, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      ParentComponent: this.analyticsMetadata?.parentComponentName,
      Name: item.label,
      Disabled: item.disabled,
      Type: item.routerLink ? 'route' : 'action',
      SiblingsOptionsCount: this._getSiblingsCount(),
      HiddenSiblingsOptionsCount: this._getHiddenSiblingsCount()
    });

    if (item.disabled) {
      return;
    } else {
      if (this.makeItemActive) {
        this._menuGlobalService.selectMenuItem(item);
      }

      if (item.closeOnClick !== false) {
        this._menuInnerService.detachCascade();
      }
    }
  }

  getClass(): string {
    if (this.isMobile) {
      return;
    }

    if (this.level === 0) {
      return `a-dropdown-menu a-dropdown-menu__${this.theme}`;
    }
  }

  onFiltering(): void {
    this.filteredNodes = this._adaptTreeStructureFilteringService.filter(this.nodes, this.filterQuery, 'label');
    this.resultsFound = Boolean(this.filteredNodes && this.filteredNodes.length);

    // The main goal of this peace of code is to re-order QueryList of focusable items from Angular ListKeyManager
    // to have possibility to navigate by order from DOM
    // In case of tree structure it means that items will be navigated one by one no meter of nesting(like flat structure)
    this._ngZone.runOutsideAngular(() => {
      setTimeout(() => {
        if (this.filterResults && this.filterResults.nativeElement) {
          const focusMonitoringArr = this.focusMonitoring.toArray();
          const filteredItems = this.filterResults.nativeElement.querySelectorAll('li');

          if (filteredItems && filteredItems.length) {
            const filteredItemsArr = Array.prototype.slice.call(filteredItems);
            const orderedListOfIds = filteredItemsArr.map(item => item.id);

            focusMonitoringArr.sort((a, b) => orderedListOfIds.indexOf(a.optionId) - orderedListOfIds.indexOf(b.optionId));
            this.focusMonitoring.reset(focusMonitoringArr);
          }
        }
      }, 100);
    });

    SafeCdr.detectChanges(this._cd);
  }

  onClearFilter(): void {
    this.filterQuery = '';
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  isEventFrom(event: any): boolean {
    return isEventFrom(event, this._elemRef.nativeElement);
  }

  closeMenu(): void {
    if (!this.isMobile) {
      this._menuGlobalService.closeMenu();
    } else {
      this._menuInnerService.detachCascade();
    }
  }

  private _applyPosition(): void {
    positionElements(this.anchor._elemRef.nativeElement, this._elemRef.nativeElement,
      this.firstInit ? this._placementRootMenu : this._placementSubmenu,
      this._menuInnerService.appendToBody);

    if (!this.firstInit) {
      const top = getComputedStyle(this._elemRef.nativeElement).top;

      this._renderer.setStyle(this._elemRef.nativeElement, 'top', `${parseInt(top, 10) - this._shiftTop}px`);
    }
  }

  private _getSiblingsCount(): number {
    return this.nodes ? this.nodes.filter(menuItem => !menuItem.hide).length : 0;
  }

  private _getHiddenSiblingsCount(): number {
    return this.nodes ? this.nodes.filter(menuItem => menuItem.hide).length : 0;
  }

  generateSubmenuItemTestID(item: MenuItem): string {
    if (!item.testID) {
      item.testID = `submenu-item-test-id-${_submenuItemTestIdIndex++}`;
    }

    return item.testID;
  }
}


@Directive({
  selector: '[adaptMenuAnchor]',
})
export class AdaptMenuAnchorDirective {
  @HostBinding('class.menu-toggle') defaultHostClass: boolean = true;

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  constructor(@Inject(forwardRef(() => AdaptMenuDirective)) private menu: any,
              public _elemRef: ElementRef) {
  }

  @HostListener('mouseenter', ['$event'])
  mouseEnter(e: Event): void {
    if (this.menu.isMobile || this.menu.firstInit || this.menu.node.expanded) {
      return;
    }
    this.menu.eventType = 'mouse';
    this.toggleOpen(e);
  }

  @HostListener('click', ['$event'])
  click(e: MouseEvent): void {
    if (this.menu.firstInit || this.menu.isMobile) {
      this.menu.eventType = getInteractionSource(e);
      this.toggleOpen(e);
    }
  }

  @HostListener('keydown', ['$event'])
  handleKeyDown(event: KeyboardEvent): void {
    const keyCode = getEventKeyCode(event);

    if ((keyCode === (this.menu.isRtl ? LEFT_ARROW : RIGHT_ARROW)) && !this.menu.firstInit) {
      this.menu.eventType = 'keyboard';
      this.menu.extendCascade();
    }
  }

  toggleOpen(e: Event): void {
    if (e.type === 'keydown') {
      e.stopPropagation();
    }

    this.menu.extendCascade();
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  isEventFrom(event: any): boolean {
    return isEventFrom(event, this._elemRef.nativeElement);
  }
}


@Component({
  selector: 'adapt-menu-renderer, [adaptMenu]',
  exportAs: 'adaptMenu',
  template: `
    <ng-content select="[adaptMenuAnchor]"></ng-content>

    <ng-template #contentPlacement></ng-template>
  `,
})
// eslint-disable-next-line @angular-eslint/component-class-suffix
export class AdaptMenuDirective implements OnInit, OnDestroy {
  @Input() node: MenuItem;
  @Input() theme: string;
  @Input() subMenuHeader: string;
  @Input() filteringConfig: MenuItemFilteringConfig;
  @Input() closePrevious: boolean;
  @Input() makeItemActive: boolean;
  @Input() analyticsMetadata: {
    parentComponentName: AdaptRadarAngularCustomEventName;
  } = null;

  @HostBinding('class.submenu') defaultHostClass: boolean = true;

  firstInit: boolean;
  cascadeId: string;
  level: number;
  isMobile: boolean;
  isRtl: boolean = false;
  menuWindowRef: ComponentRef<AdaptSubmenuComponent>;
  eventType: FocusOrigin = null;

  @ViewChild('contentPlacement', {read: ViewContainerRef, static: true}) contentPlacement: ViewContainerRef;
  @ContentChild(AdaptMenuAnchorDirective) private _anchor: AdaptMenuAnchorDirective;

  get cascade(): CascadeItem {
    return this._menuInnerService.cascadeChain;
  }

  private _popupWindowService: PopupService<AdaptSubmenuComponent>;

  constructor(private _componentFactoryResolver: ComponentFactoryResolver,
              private _deviceDetector: AdaptDeviceDetectionService,
              private _el: ElementRef,
              private _injector: Injector,
              private _renderer: Renderer2,
              private _layerService: LayerService,
              private _menuInnerService: MenuInnerService,
              private _menuGlobalService: MenuGlobalService,
              private _dir: Directionality,
              private _applicationRef: ApplicationRef) {
  }

  ngOnInit(): void {
    this.isRtl = this._dir.value === 'rtl';
    this.level = this.getCascadeLength();
    this.firstInit = (this.level === 0);
    this.cascadeId = this._menuInnerService.id;
    this.isMobile = this._menuInnerService.isMobile;

    this._popupWindowService = new PopupService<AdaptSubmenuComponent>(
      AdaptSubmenuComponent,
      this._injector,
      this.contentPlacement,
      this._renderer,
      this._componentFactoryResolver,
      this._applicationRef);
  }

  ngOnDestroy(): void {
    if (this.menuWindowRef) {
      this._popupWindowService.close();
      this.menuWindowRef = null;
    }
  }

  getCascadeLength(): number {
    return this.cascade ? this.cascade.subMenuItems.length : 0;
  }

  extendCascade(): void {
    this._toggleRootCascadeItem();
    this._toggleNestedCascadeItem();
  }

  private _toggleRootCascadeItem(): void {
    if (!this.firstInit) {
      return;
    }

    if (this.cascade) {
      if (this.node.expanded) {
        this._menuInnerService.detachCascade();
        this.node.expanded = false;

        this._popupWindowService.close();
        this.menuWindowRef = null;
      } else {
        this._createCascadeItem();
      }

      return;
    }

    this._createCascadeItem();
  }

  private _toggleNestedCascadeItem(): void {
    if (this.firstInit) {
      return;
    }

    if (!this.node.expanded && this._menuInnerService.cascadeChain.subMenuItems.length > this.level) {
      this._menuInnerService.destroyCascadeItems(this.level - 1);

      this._createCascadeItem();

      return;
    }

    if (!this.node.expanded && this._menuInnerService.cascadeChain.subMenuItems.length === this.level) {
      this._createCascadeItem();

      return;
    }


    this._menuInnerService.destroyCascadeItems(this.level - 1);
    this._popupWindowService.close();
    this.menuWindowRef = null;
  }

  private _createCascadeItem(): void {
    if (this.menuWindowRef) {
      this._popupWindowService.close();
    }

    if (this.level === 0 && this.closePrevious) {
      // Close previously opened menu
      this._menuGlobalService.closeMenu();
    }
    this.menuWindowRef = this._popupWindowService.open();

    this.menuWindowRef.instance.nodes = this.node.items || this.node.subMenu;
    this.menuWindowRef.instance.filteringConfig = this.filteringConfig;
    this.menuWindowRef.instance.subMenuHeader = this.node.header;
    this.menuWindowRef.instance.level = this.level;
    this.menuWindowRef.instance.anchor = this._anchor;
    this.menuWindowRef.instance.firstInit = this.firstInit;
    this.menuWindowRef.instance.theme = this.theme;
    this.menuWindowRef.instance.isMobile = this.isMobile;
    this.menuWindowRef.instance.eventType = this.eventType;
    this.menuWindowRef.instance.makeItemActive = this.makeItemActive;
    this.menuWindowRef.instance.analyticsMetadata = this.analyticsMetadata;

    this.node.expanded = true;

    if (this.node.disabled && this.node.items) {
      this.node.items.forEach(element => {
        element.disabled = true;
      });
    }
    this._menuInnerService.addCascadeItem(this.node, this.menuWindowRef, this._anchor);

    if (this.closePrevious) {
      // Save newly opened menu so it can be closed later if other menu will be opened
      this._menuGlobalService.storeMenu(this.menuWindowRef, this.node);
    }
  }
}
